<template>
  <div>
    <div>
      <!--信息展示table-->
      <el-table
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column
          label="编号"
          width="180">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="姓名"
          width="180">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ scope.row.name }}</p>
              <p>住址: {{ scope.row.address }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.name }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="生日" prop="birth"></el-table-column>
        <el-table-column label="性别" prop="sex"></el-table-column>
        <el-table-column label="住址" prop="address"></el-table-column>
        <el-table-column align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入姓名进行搜索"/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>

            <el-popconfirm title="这是一段内容确定删除吗？" @confirm="handleDelete(scope.$index, scope.row)">
              <el-button slot="reference" size="mini" type="danger">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>

      <el-button style="margin: 10px 0;" type="success" size="mini" @click="add">添加</el-button>

      <transition name="el-zoom-in-center">
        <div v-show="show" class="transition-box">

          <!--信息添加修改表单-->
          <el-form :hide-required-asterisk="false" :rules="rules" ref="form" :model="form" label-suffix=":" label-width="80px">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
            </el-form-item>
            <el-form-item label="生日" prop="birth">
              <!--在使用日期组件是必须添加一个value-format属性，否则提交后的数据会默认少一天-->
              <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="选择日期" v-model="form.birth" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="性别">
              <el-radio-group v-model="form.sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="详细地址" prop="address">
              <el-input type="textarea" v-model="form.address"  placeholder="请输入居住地址"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">提交</el-button>
              <el-button @click="resetForm">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    name: "List",
    data() {
      return {
        tableData: [],
        search: '',
        show: false,
        form: {
          name: '',
          birth: '',
          sex: '',
          address: ''
        },
        rules: {
          name: [{required: true, message: '请输入用户名', trigger: 'blur'}]
        }
      }
    },
    methods: {
      handleEdit(index, row) {
        this.show = true
        this.form = row
      },
      handleDelete(index, row) {
        this.$http.get("http://localhost:8089/delete?id=" + row.id).then(res => {
          if(res.data > 0){
            this.$message({
              message: '信息删除成功',
              type: 'success'
            })
            this.selectInfo()
          }else{
            this.$message.error("信息删除失败请重试！！！")
          }
        })
      },
      add() {
        this.show = !this.show
        this.form = {}
      },
      onSubmit() {
        this.$http.post("http://localhost:8089/saveORupdate", this.form).then(res => {
          if(res.data.rows > 0){
            this.$message({
              message: res.data.msg,
              type: 'success'
            })
            this.show = false
            this.selectInfo()
          }else{
            this.$message.error("该操作失败，请重试！！！")
          }
        })
      },
      resetForm() {
        this.form = {}
      },
      selectInfo() {
        this.$http.get("http://localhost:8089/findAll").then(res => {
          this.tableData = res.data
        })
      }
    },
    created() {
      this.selectInfo()
    }
  }
</script>

<style scoped>
  .transition-box {
    margin-bottom: 10px;
    width: 100%;
    height: 600px;
    border-radius: 4px;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-right: 20px;
  }
</style>



